<script>
import  firebase  from 'firebase' 
import { db } from "../db.js";
import barra from "./barra.vue";
  export default  {
     name:"inicio",
    components: {
      barra
    },
    data() {
      return {
        atletismo:[],
        slide: 0,
        sliding: null,
         nuevoComentario:{
         
        }, 
      }
    },
  firestore: {
    atletismo: db.collection('foro'), 
    },
    methods: {
    introducirNota: function () {
      db.collection('foro').add({
        Texto: this.nuevoComentario.Texto,
        Autor: firebase.auth().currentUser.email ,
        });
      this.nuevoComentario.Texto="";
      this.nuevoComentario.Autor="";
      },

      onSlideStart() {
        this.sliding = true
      },
      onSlideEnd() {
        this.sliding = false
      }
    }
  }
</script>

