<template lang="html">
<div>
  <barra></barra>
  <h1 id="titulo" class="p-3 mb-2 bg-dark text-white" style="margin-bottom: 8px font-size:50px">Bienvenido a RunForever</h1>


  <div class="container" >
    <div class="row mt-5">
      <div class="col-sm-2">
        </div>
      <div class="col-sm-8 text-center pading-top=3em">
        <div class="card">
          <div class="car-header" style="padding-top:10px">
           <h1 id="titulo" style="margin-bottom: 8px font-size:50px">Busca tu prueba</h1>
          </div>
          <div class="car-body" id="buscador">
            <input type="text" class="form-control" id="cuadroTexto" v-model="filtrar">
          </div>
          <!-- <div class="car-body">
            <form @submit.prevent="introducirPrueba">
                <div class="form-group">
                  <input type="text" class="form-control" v-model="nuevaNota.Texto" placeholder="Texto">
                </div>
                <div class="form-group">
                  <input type="file" class="form-control-file" @change="subirFicheros" placeholder="Seleccionar archivo">
                </div>
                 <div class="form-group" id="boton">
                  <button type="submit" @change="introducirPrueba" class="btn btn-primary">Aadir</button>
                  </div>
              </form>
          </div> -->
        </div>
      </div>
    </div>

    <div class="container" style="padding-top:60px">
    

      <div class="col-sm-12 text-center">
        <div class="card">
          <div class="card-header">
            <h1 id="titulo" style="margin-bottom: 8px font-size:50px">Listado de competiciones</h1>
          </div>
          <div class="card-body">
            <table class="table table-stripped table-bordered" id="tablaColor" >
                <tr>
                  <th>Prueba</th>
                  <th>Archivo</th>
                  <!-- <td></td> -->
                </tr>
                <tr v-for="n in filtrarPalabras" v-bind:key="n.Texto">
                  <td>{{n.Texto}}</td>
                  <td><a :href="n.Archivo['URL']">{{n.Archivo["Nombre"]}}</a></td> 
                  <!-- <td><button class="btn btn-danger" @click="borrarNota(n.id)">X</button></td> -->
                </tr>
            </table>
          </div>
        </div>
      </div>
    

    </div>
  </div>

</div>
  

</template>


<script lang="js">
import  firebase  from 'firebase' 
import { db } from "../db.js";
import barra from "./barra.vue";
  export default  {
     name:"privado",
    components: {
      barra
    },
    mounted () {
    },
    data () {
      return {
        atletismo:[],
        subirArchivo:null,
        filtrar:'',
        nuevaNota:{
          Autor:'',
          Texto:'',
          Archivo:{
            Nombre:'',
            URL:''
          }
        }, 
      }
    },
    firestore: {
      atletismo: db.collection('listaPruebas'),
      
    },
    methods: {
      subirFicheros: function(event){
        this.subirArchivo=event.target.files[0]
      },
      introducirPrueba: function () {
        firebase.storage().ref(this.subirArchivo.name).put(this.subirArchivo);
        firebase.storage().ref().child(this.subirArchivo.name).getDownloadURL().then(
          url=>{
            this.subirArchivo.url=url
             db.collection('listaPruebas').add({
               Texto: this.nuevaNota.Texto,
               Autor: firebase.auth().currentUser.email ,
               Archivo:{
                 Nombre: this.subirArchivo.name,
                 URL: url
               }
              });
              
            this.nuevaNota.Texto="";
            this.nuevaNota.Autor="";
          });
       
      },
      borrarNota: function(id){
        db.collection('listaPruebas').doc(id).delete()
      },
      desconectar: function () {
        firebase.auth().signOut().then(() => {
          this.$router.replace('login')
        })
      }
      
    },
    computed: {
       usuario:function(){
          return firebase.auth().currentUser;
      },
      filtrarPalabras: function () {
      var vm = this;
      return this.atletismo.filter(       
        function (n) {
        return n.Texto.toLowerCase().indexOf(vm.filtrar.toLowerCase()) !== -1
      })
    }
    }
}


</script>

<style scoped >
.privado {
}

button {
  width: 35%;
}
form-group,
input {
  margin: 1em;
  width: 90%;
}

#titulo {
  color: rgb(219, 48, 48);
  font-weight: bold;
}
#buscador{
  text-align: center;
}
#boton button {
  text-align: center;
}

#tablaColor tr:hover {
  background-color: rgb(238, 238, 147);
  font-weight: bold;
}
#cuadrotexto{
  
    margin-bottom: 33px;
}
</style>
